/**
 * GEPTManager - GEPTÂàÜÁ¥öÂíåÂÖßÂÆπÊ®°ÊùøÁÆ°ÁêÜÁ≥ªÁµ±
 * ÈÅ∑ÁßªÂà∞ Vite + Phaser 3 Â∞àÊ°à
 */

export type GEPTLevel = 'elementary' | 'intermediate' | 'high-intermediate';

export interface GEPTWord {
  id: string;
  english: string;
  chinese: string;
  level: GEPTLevel;
  frequency: number; // ‰ΩøÁî®È†ªÁéá 1-100
  difficulty?: number; // Èõ£Â∫¶ 1-10
  partOfSpeech?: string;
  pronunciation?: string;
  category?: string;
}

export interface ValidationResult {
  isValid: boolean;
  errors: ValidationError[];
  warnings: ValidationWarning[];
  suggestions: string[];
  geptCompliance: {
    level: GEPTLevel;
    score: number; // 0-100
    issues: string[];
  };
}

export interface ValidationError {
  type: 'grammar' | 'spelling' | 'format' | 'gept-compliance';
  message: string;
  position?: { start: number; end: number };
  severity: 'error' | 'warning' | 'info';
  suggestion?: string;
}

export interface ValidationWarning {
  type: 'complexity' | 'vocabulary' | 'structure';
  message: string;
  suggestion: string;
}

/**
 * GEPT ÁÆ°ÁêÜÂô®È°û
 */
export class GEPTManager {
  private wordDatabase: Map<GEPTLevel, GEPTWord[]> = new Map();
  private currentLevel: GEPTLevel = 'elementary';

  constructor() {
    this.initializeWordDatabase();
  }

  /**
   * ÂàùÂßãÂåñË©ûÂΩôÊï∏ÊìöÂ∫´
   */
  private initializeWordDatabase(): void {
    // Elementary Á¥öÂà•Ë©ûÂΩô
    const elementaryWords: GEPTWord[] = [
      { id: '1', english: 'hello', chinese: '‰Ω†Â•Ω', level: 'elementary', frequency: 100 },
      { id: '2', english: 'world', chinese: '‰∏ñÁïå', level: 'elementary', frequency: 95 },
      { id: '3', english: 'book', chinese: 'Êõ∏', level: 'elementary', frequency: 90 },
      { id: '4', english: 'school', chinese: 'Â≠∏Ê†°', level: 'elementary', frequency: 85 },
      { id: '5', english: 'friend', chinese: 'ÊúãÂèã', level: 'elementary', frequency: 80 },
      { id: '6', english: 'family', chinese: 'ÂÆ∂Â∫≠', level: 'elementary', frequency: 85 },
      { id: '7', english: 'house', chinese: 'ÊàøÂ≠ê', level: 'elementary', frequency: 82 },
      { id: '8', english: 'water', chinese: 'Ê∞¥', level: 'elementary', frequency: 88 },
      { id: '9', english: 'food', chinese: 'È£üÁâ©', level: 'elementary', frequency: 86 },
      { id: '10', english: 'time', chinese: 'ÊôÇÈñì', level: 'elementary', frequency: 92 }
    ];

    // Intermediate Á¥öÂà•Ë©ûÂΩô
    const intermediateWords: GEPTWord[] = [
      { id: '11', english: 'environment', chinese: 'Áí∞Â¢É', level: 'intermediate', frequency: 75 },
      { id: '12', english: 'technology', chinese: 'ÊäÄË°ì', level: 'intermediate', frequency: 78 },
      { id: '13', english: 'education', chinese: 'ÊïôËÇ≤', level: 'intermediate', frequency: 80 },
      { id: '14', english: 'government', chinese: 'ÊîøÂ∫ú', level: 'intermediate', frequency: 72 },
      { id: '15', english: 'economy', chinese: 'Á∂ìÊøü', level: 'intermediate', frequency: 70 }
    ];

    // High-Intermediate Á¥öÂà•Ë©ûÂΩô
    const highIntermediateWords: GEPTWord[] = [
      { id: '16', english: 'sophisticated', chinese: 'Ë§áÈõúÁöÑ', level: 'high-intermediate', frequency: 60 },
      { id: '17', english: 'phenomenon', chinese: 'ÁèæË±°', level: 'high-intermediate', frequency: 58 },
      { id: '18', english: 'hypothesis', chinese: 'ÂÅáË®≠', level: 'high-intermediate', frequency: 55 },
      { id: '19', english: 'methodology', chinese: 'ÊñπÊ≥ïË´ñ', level: 'high-intermediate', frequency: 52 },
      { id: '20', english: 'comprehensive', chinese: 'ÂÖ®Èù¢ÁöÑ', level: 'high-intermediate', frequency: 65 }
    ];

    this.wordDatabase.set('elementary', elementaryWords);
    this.wordDatabase.set('intermediate', intermediateWords);
    this.wordDatabase.set('high-intermediate', highIntermediateWords);

    console.log('üìö GEPT Ë©ûÂΩôÊï∏ÊìöÂ∫´ÂàùÂßãÂåñÂÆåÊàê');
  }

  /**
   * Ë®≠ÁΩÆÁï∂Ââç GEPT Á≠âÁ¥ö
   */
  setLevel(level: GEPTLevel): void {
    this.currentLevel = level;
    console.log(`üìä Ë®≠ÁΩÆ GEPT Á≠âÁ¥ö: ${level}`);
  }

  /**
   * Áç≤ÂèñÁï∂ÂâçÁ≠âÁ¥ö
   */
  getCurrentLevel(): GEPTLevel {
    return this.currentLevel;
  }

  /**
   * Áç≤ÂèñÊåáÂÆöÁ≠âÁ¥öÁöÑË©ûÂΩô
   */
  getWordsForLevel(level: GEPTLevel): GEPTWord[] {
    return this.wordDatabase.get(level) || [];
  }

  /**
   * Áç≤ÂèñÁï∂ÂâçÁ≠âÁ¥öÁöÑË©ûÂΩô
   */
  getCurrentLevelWords(): GEPTWord[] {
    return this.getWordsForLevel(this.currentLevel);
  }

  /**
   * Áç≤ÂèñÈö®Ê©üË©ûÂΩô
   */
  getRandomWord(level?: GEPTLevel): GEPTWord | null {
    const targetLevel = level || this.currentLevel;
    const words = this.getWordsForLevel(targetLevel);
    
    if (words.length === 0) {
      console.warn(`‚ö†Ô∏è Ê≤íÊúâÊâæÂà∞ ${targetLevel} Á≠âÁ¥öÁöÑË©ûÂΩô`);
      return null;
    }

    const randomIndex = Math.floor(Math.random() * words.length);
    return words[randomIndex];
  }

  /**
   * Áç≤ÂèñÂ§öÂÄãÈö®Ê©üË©ûÂΩô
   */
  getRandomWords(count: number, level?: GEPTLevel): GEPTWord[] {
    const targetLevel = level || this.currentLevel;
    const words = this.getWordsForLevel(targetLevel);
    
    if (words.length === 0) return [];

    const shuffled = [...words].sort(() => 0.5 - Math.random());
    return shuffled.slice(0, Math.min(count, words.length));
  }

  /**
   * Ê†πÊìö ID Êü•ÊâæË©ûÂΩô
   */
  findWordById(id: string): GEPTWord | null {
    for (const [level, words] of this.wordDatabase) {
      const word = words.find(w => w.id === id);
      if (word) return word;
    }
    return null;
  }

  /**
   * Ê†πÊìöËã±ÊñáÂñÆÂ≠óÊü•ÊâæË©ûÂΩô
   */
  findWordByEnglish(english: string): GEPTWord | null {
    for (const [level, words] of this.wordDatabase) {
      const word = words.find(w => w.english.toLowerCase() === english.toLowerCase());
      if (word) return word;
    }
    return null;
  }

  /**
   * È©óË≠âË©ûÂΩôÊòØÂê¶Á¨¶Âêà GEPT Ê®ôÊ∫ñ
   */
  validateWord(word: string, level: GEPTLevel): ValidationResult {
    const foundWord = this.findWordByEnglish(word);
    
    if (!foundWord) {
      return {
        isValid: false,
        errors: [{
          type: 'gept-compliance',
          message: `Ë©ûÂΩô "${word}" ‰∏çÂú® GEPT ${level} Ë©ûÂΩôË°®‰∏≠`,
          severity: 'error',
          suggestion: 'Ë´ã‰ΩøÁî® GEPT Ê®ôÊ∫ñË©ûÂΩô'
        }],
        warnings: [],
        suggestions: [],
        geptCompliance: {
          level,
          score: 0,
          issues: [`Ë©ûÂΩô "${word}" Êú™ÊâæÂà∞`]
        }
      };
    }

    const isCorrectLevel = foundWord.level === level;
    
    return {
      isValid: isCorrectLevel,
      errors: isCorrectLevel ? [] : [{
        type: 'gept-compliance',
        message: `Ë©ûÂΩô "${word}" Â±¨Êñº ${foundWord.level} Á≠âÁ¥öÔºå‰∏çÁ¨¶Âêà ${level} Á≠âÁ¥öË¶ÅÊ±Ç`,
        severity: 'warning',
        suggestion: `Âª∫Ë≠∞‰ΩøÁî® ${level} Á≠âÁ¥öË©ûÂΩô`
      }],
      warnings: [],
      suggestions: [],
      geptCompliance: {
        level: foundWord.level,
        score: isCorrectLevel ? 100 : 50,
        issues: isCorrectLevel ? [] : [`Á≠âÁ¥ö‰∏çÂåπÈÖç: ${foundWord.level} vs ${level}`]
      }
    };
  }

  /**
   * Áç≤ÂèñË©ûÂΩôÁµ±Ë®à‰ø°ÊÅØ
   */
  getStatistics(): { [key in GEPTLevel]: number } {
    return {
      elementary: this.wordDatabase.get('elementary')?.length || 0,
      intermediate: this.wordDatabase.get('intermediate')?.length || 0,
      'high-intermediate': this.wordDatabase.get('high-intermediate')?.length || 0
    };
  }

  /**
   * Ê∑ªÂä†Êñ∞Ë©ûÂΩô
   */
  addWord(word: GEPTWord): boolean {
    const words = this.wordDatabase.get(word.level);
    if (!words) return false;

    // Ê™¢Êü•ÊòØÂê¶Â∑≤Â≠òÂú®
    const exists = words.some(w => w.english.toLowerCase() === word.english.toLowerCase());
    if (exists) {
      console.warn(`‚ö†Ô∏è Ë©ûÂΩô "${word.english}" Â∑≤Â≠òÂú®`);
      return false;
    }

    words.push(word);
    console.log(`‚úÖ Ê∑ªÂä†Ë©ûÂΩô: ${word.english} (${word.level})`);
    return true;
  }
}
